<template>
  <div class="system-usermanagement-reset_super_pass">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="100"
      ref="system_usermanagement_reset_super_pass"
    >
      <mu-form-item prop="password" label="新密码" :rules="passwordRules">
        <mu-text-field
          :action-icon="visibility ? 'visibility_off' : 'visibility'"
          :action-click="() => (visibility = !visibility)"
          :type="visibility ? 'text' : 'password'"
          v-model="formData.password"
          :max-length="50"
        ></mu-text-field>
      </mu-form-item>
      <div class="form-beizhu">
        备注：密码至少包含大小写字母、数字、特殊字符(~!@#$%^&*()_-+,.)中的三种,且长度为8-24位
      </div>
      <mu-form-item>
        <mu-button color="primary" small @click="submitEdit">修改</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { resetSuperpass } from "req/api/system/usermanagement.js";
export default {
  name: "system_usermanagement_reset_super_pass",
  data() {
    return {
      visibility: false,
      labelPosition: "right",
      passwordRules: [
        { validate: val => !!val, message: "密码不能为空" },
        {
          validate: val => this.$validates.passwordReg.test(val),
          message: "密码格式不正确"
        }
      ],
      formData: {
        password: ""
      }
    };
  },
  props: {
    layerid: {
      type: String
    }
  },
  methods: {
    submitEdit() {
      this.$refs.system_usermanagement_reset_super_pass
        .validate()
        .then(result => {
          if (result) {
            this.$isShowTip(
              "system",
              "system_usermanagement",
              "确定重置吗?",
              () => {
                let data = JSON.parse(JSON.stringify(this.formData));
                data.password = this.$md5(this.formData.password);
                this.__resetSuperpass(data);
              },
              this
            );
          }
        });
    },
    cancel() {
      this.$isShowTip(
        "system",
        "system_usermanagement",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __resetSuperpass(param) {
      resetSuperpass(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "重置成功"
          });
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.system-usermanagement-reset_super_pass {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>



