<template>
  <div class="system-usermanagement">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="system_usermanagement_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="loginConfig"
          >登录设置</el-button
        >
        <el-button type="primary" size="mini" @click="add">增加</el-button>
        <el-button type="danger" size="mini" @click="del(selectData)"
          >批量删除</el-button
        >
        <el-button type="danger" size="mini" @click="resetSuperPass()"
        v-if="level == 0"
          >重置超级管理员密码</el-button
        >
      </template>
      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button type="primary" size="mini" @click="edit(props.data.row)"
          >编辑</el-button
        >
      </template>
      <!-- 用户昵称 -->
      <template v-slot:show_name_head="props">
        <el-input
          v-model="params.show_name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 用户名 -->
      <template v-slot:real_name_head="props">
        <el-input
          v-model="params.real_name"
          size="mini"
          placeholder="请输入内容"
          @keyup.enter.native="search"
          :maxLength="20"
        ></el-input>
      </template>
      <!-- 创建时间 -->
      <template v-slot:ctime_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          v-model="params.ctime"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
      <!-- 最后登录时间 -->
      <template v-slot:last_login_time_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          v-model="params.last_login_time"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import { delUser } from "req/api/system/usermanagement.js";
import loginConfig from './login_config'
import add from './add'
import resetSuperPass from './resetSuperPass'
export default {
  data() {
    return {
      url: "/user/list",
      type: "get",
      level:1,
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "用户昵称",
          sortable: true,
          prop: "show_name",
          second_head: {
            prop: "show_name",
            width:"200px",
            templateHeadName: "show_name_head"
          }
        },
        {
          label: "用户名",
          sortable: true,
          prop: "real_name",
          second_head: {
            prop: "real_name",
            width:"200px",
            templateHeadName: "real_name_head"
          }
        },
        {
          label: "创建时间",
          prop: "ctime",
          sortable: true,
          second_head: {
            width:"200px",
            templateHeadName: "ctime_head",
            prop: "ctime"
          }
        },
        {
          label: "最后登录时间",
          prop: "last_login_time",
          sortable: true,
          second_head: {
            width:"200px",
            templateHeadName: "last_login_time_head",
            prop: "last_login_time"
          }
        },
        {
          label: "操作",
          second_head: {
            label: "",
            minWidth: "100px",
            isTemplate: true,
            templateName: "operation"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        real_name: "",
        show_name:"",
        ctime:"",
        last_login_time: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      selectData: []
    };
  },
  activated() {
    this.level = sessionStorage.getItem('level')
  },
  methods: {
    reload() {
      this.$refs.system_usermanagement_table.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
      this.selectData = select
    },
    loginConfig() {
      this.$layer.iframe({
        content: {
          content: loginConfig,
          parent: this,
          data: {}
        },
        area: ["400px", "auto"],
        title: "登录设置",
        shadeClose: false
      });
    },
    resetSuperPass() {
      this.$layer.iframe({
        content: {
          content: resetSuperPass,
          parent: this,
          data: {}
        },
        area: ["500px", "auto"],
        title: "重置超级管理员密码",
        shadeClose: false
      });
    },
    add() {
      this.$layer.iframe({
        content: {
          content: add,
          parent: this,
          data: {
            isAdd:true
          }
        },
        area: ["500px", "auto"],
        title: "添加用户信息",
        shadeClose: false
      });
    },
    edit(item) {
      this.$layer.iframe({
        content: {
          content: add,
          parent: this,
          data: {
            isAdd:false,
            layer_data:item
          }
        },
        area: ["500px", "auto"],
        title: "修改用户信息",
        shadeClose: false
      });
    },
    del(item) {
      let ids = ""
      if(Array.isArray(item)) {
        if(this.$tools.isArrayNull(item)) {
          this.$message({
            type:"error",
            message:"请选择需要删除的选项!"
          })
          return
        }
        ids = this.$tools.mergeId(item)
      }else {
        ids = item.id.toString()
      }
      this.$isShowTip(
        "system",
        "system_usermanagement",
        "确定删除吗?",
        () => {
          this.__delUser({
            id:ids
          })
        },
        this
      );
    },
    __delUser(param) {
      delUser(param).then(res => {
        this.$message({
          type:"success",
          message:"删除成功"
        })
        this.reload()
      }).catch(err => {})
    }
  }
};
</script>



