<template>
  <div class="system-usermanagement-add">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="100"
      ref="system_usermanagement_add"
    >
      <mu-form-item prop="real_name" label="用户名" v-if="isAdd" :rules="real_nameRules">
        <mu-text-field v-model="formData.real_name" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="show_name" label="用户昵称" :rules="show_nameRules">
        <mu-text-field v-model="formData.show_name" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="password" label="密码" :rules="isAdd ? passwordRules : editPasswordRules">
        <mu-text-field
          :action-icon="visibility ? 'visibility_off' : 'visibility'"
          :action-click="() => (visibility = !visibility)"
          :type="visibility ? 'text' : 'password'"
          v-model="formData.password"
          :max-length="50"
        ></mu-text-field>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" small @click="submitAdd" v-if="isAdd">添加</mu-button>
        <mu-button color="primary" small @click="submitEdit" v-if="!isAdd">修改</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
      <div class="form-beizhu">
        <p>备注：</p>
        <p>
          (1).用户昵称用于用户名称的显示
          <br />(2).密码至少包含大小写字母、数字、特殊字符(~!@#$%^&*()_-+,.)中的三种,且长度为8-24位
        </p>
      </div>
    </mu-form>
  </div>
</template>
<script>
import { addUser, editUser } from "req/api/system/usermanagement.js";
export default {
  name: "system_usermanagement_add",
  data() {
    return {
      labelPosition: "right",
      visibility: false,
      real_nameRules: [
        { validate: val => !!val, message: "用户名不能为空" },
        {
          validate: val => this.$validates.nameReg.test(val),
          message:
            "用户名只能为字母、数字或下划线,且不能用下划线开头,长度3-16位"
        }
      ],
      show_nameRules: [
        { validate: val => !!val, message: "用户昵称不能为空" },
        {
          validate: val => {
            return val.length > 1 && val.length < 9;
          },
          message: "用户昵称长度为2-8位"
        }
      ],
      editPasswordRules: [
        {
          validate: val => {
            if(val) {
              return this.$validates.passwordReg.test(val)
            }else {
              return true
            }
          },
          message: "密码格式不正确"
        }
      ],
      passwordRules: [
        { validate: val => !!val, message: "密码不能为空" },
        {
          validate: val => this.$validates.passwordReg.test(val),
          message: "密码格式不正确"
        }
      ],
      formData: {
        id: "",
        real_name: "",
        show_name: "",
        password: ""
      }
    };
  },
  props: {
    layer_data: {
      type: [Object, Array, Number],
      default: () => {
        return {};
      }
    },
    isAdd: {
      type: Boolean,
      default: true
    },
    layerid: {
      type: String
    }
  },
  created() {
    if (!this.isAdd) {
      this.formData.id = this.layer_data.id;
      this.formData.real_name = this.layer_data.real_name;
      this.formData.show_name = this.layer_data.show_name;
    }
  },
  methods: {
    submitAdd() {
      this.$refs.system_usermanagement_add.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_usermanagement",
            "确定添加吗？",
            () => {
              let data = JSON.parse(JSON.stringify(this.formData));
                data.password = this.$md5(this.formData.password);
              this.__addUser(data);
            },
            this
          );
        }
      });
    },
    submitEdit() {
      this.$refs.system_usermanagement_add.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_usermanagement",
            "确定修改吗?",
            () => {
              let data = JSON.parse(JSON.stringify(this.formData));
              if(this.formData.password) {
                data.password = this.$md5(this.formData.password);
              }
              this.__editUser(data);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "system",
        "system_usermanagement",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __addUser(param) {
      addUser(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "添加成功"
          });
          this.$layer.close(this.layerid);
          this.$parent.$refs.system_usermanagement_table.reload();
        })
        .catch(err => {});
    },
    __editUser(param) {
      editUser(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "修改成功"
          });
          this.$layer.close(this.layerid);
          this.$parent.$refs.system_usermanagement_table.reload();
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.system-usermanagement-add {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>





