<template>
  <div class="system-usermanagement-login_config">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="100"
      ref="system_usermanagement_login_config"
    >
      <mu-form-item prop="fail_count" label="限制次数" :rules="fail_countRules">
        <mu-text-field
          v-model="formData.fail_count"
          :max-length="50"
          type="number"
        ></mu-text-field>
      </mu-form-item>
      <mu-form-item prop="fail_time" label="限制时间" :rules="fail_timeRules">
        <mu-select  v-model="formData.fail_time" full-width>
        <mu-option v-for="item in dropDownData.fail_time" :key="item.value" :label="item.name" :value="item.value"></mu-option>
      </mu-select>
      </mu-form-item>
      <div class="form-beizhu"><p>备注：</p><p>
        (1).限制次数：限制登录失败的次数<br>
        (2).限制时间：超过限制的登录失败次数后多久才能登录的时间
      </p>
      </div>
      <mu-form-item>
        <mu-button color="primary" small @click="submitEdit">修改</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { getConfig, setConfig } from "req/api/serverconfig/configset";
export default {
  name: "system_usermanagement_login_config",
  data() {
    return {
      labelPosition: "right",
      fail_countRules: [{ validate: val => !!val, message: "限制次数不能为空" },
      { validate: val => {
        return val > 0 && val < 11
      }, message: "限制次数至少为一次且不能超过十次" }],
      fail_timeRules: [{ validate: val => !!val, message: "限制时间不能为空" }],
      formData: {
        fail_count: "",
        fail_time: "",
        type:"login"
      },
      dropDownData: {
        fail_time: [
          {
            value:"900",
            name:"一刻钟"
          },
          {
            value:"1800‬",
            name:"半小时"
          },
          {
            value:"3600",
            name:"一小时"
          },
          {
            value:"7200",
            name:"二小时"
          },
          {
            value:"14400",
            name:"四小时"
          },
          {
            value:"21600",
            name:"六小时"
          },
          {
            value:"28800",
            name:"八小时"
          },
          {
            value:"43200",
            name:"十二小时"
          },
          {
            value:"86400",
            name:"二十四小时"
          }
        ]
      }
    };
  },
  props: {
    layerid: {
      type: String
    }
  },
  created () {
    this.__getConfig({
      type:"login"
    })
  },
  methods: {
    submitEdit() {
      this.$refs.system_usermanagement_login_config.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_usermanagement",
            "确定修改吗?",
            () => {
              this.__setConfig(this.formData);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "system",
        "system_usermanagement",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __getConfig(param) {
      getConfig(param)
        .then(res => {
          this.formData.fail_time = res.data.fail_time
          this.formData.fail_count = res.data.fail_count
        })
        .catch(err => {});
    },
    __setConfig(param) {
      setConfig(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "设置成功"
          });
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.system-usermanagement-login_config {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>



